.boxCmp2compare {
    position: relative;
    border: 1px solid var(--color-setting-modal);
    width: 48%;
    border-radius: 7px;
   
    color: var(--color-setting-modal);
    background: var(--bgcolor-setting-modal);
}



.boxCmp2compare .boxCmp2compare_title{
	font-family: "Google Sans","GoogleSans-Local";
	font-weight: 600;
	font-size: 18px;
	padding: 14px;
	color: var(--hrm_palette-static-overlay-touch-response);
	border-top-left-radius: 4px;
	border-top-right-radius: 4px;
}

.boxCmp2compare .boxCmp2compare_body{
	padding: 8px;
}


.boxCmp2compare .lbl{
	font-family: "Google Sans","GoogleSans-Local";
	font-weight: 400;
}



.boxCmp2compare .value{
	margin-left:5px;
	font-family: "Google Sans","GoogleSans-Local";
	font-weight: 600;
}
.boxCmp2compare .value.small{
	font-family: "Google Sans","GoogleSans-Local";
	font-weight: 400;
	font-size: 12px;
}


.boxCmp2compare.green {
    --color-setting-modal: var(--hrm_palette-success-fg);
    --bgcolor-setting-modal: var(--hrm_palette-success-100);
}

.boxCmp2compare.green .boxCmp2compare_title{
	background-color: var(--hrm_palette-success-fg);
}

.boxCmp2compare.purple {
    --color-setting-modal: var(--hrm_palette-blue-purple);
    --bgcolor-setting-modal: var(--hrm_palette-blue-purple_10);
}

.boxCmp2compare.purple .boxCmp2compare_title{
	background-color: var(--hrm_palette-blue-purple);
}


